"use client";

import React, { useState, useEffect } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import ClickSpark from "./ClickSpark";

export default function SiteNav() {
  const pathname = usePathname();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const navItems = [
    { label: "Home", href: "/" },
    { label: "About", href: "/about" },
    { label: "Projects", href: "/projects" },
    { label: "Certificates", href: "/certificates" },
    { label: "Contact", href: "/contact" },
  ];

  // Close menu on route change
  useEffect(() => {
    if (isMenuOpen) {
      setIsMenuOpen(false);
    }
  }, [pathname]);

  // Prevent scrolling when menu is open
  useEffect(() => {
    if (isMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }
    return () => {
      document.body.style.overflow = 'auto';
    };
  }, [isMenuOpen]);

  return (
    <nav className="fixed inset-x-0 top-0 z-50 bg-black/30 backdrop-blur-sm border-b border-white/10">
      <ClickSpark className="block">
        <div className="mx-auto max-w-6xl px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between py-3 sm:py-4">
            {/* Logo / Brand */}
            <Link href="/" className="font-semibold tracking-tight text-white">
              MyPortfolio
            </Link>

            {/* Desktop Links */}
            <div className="hidden sm:flex items-center gap-6">
              {navItems.map((item) => {
                const isActive = (pathname || "/") === item.href;
                const linkClass = isActive
                  ? "text-white"
                  : "text-white/70 hover:text-white transition-colors";
                const underlineClass = isActive
                  ? "w-full" 
                  : "w-0 group-hover:w-full";
                return (
                  <span key={item.href} className="relative group inline-flex">
                    <Link
                      href={item.href}
                      className={linkClass}
                      aria-current={isActive ? "page" : undefined}
                    >
                      {item.label}
                    </Link>
                    <span
                      className={`pointer-events-none absolute -bottom-1 left-0 h-0.5 bg-white/80 ${underlineClass} transition-[width] duration-300 ease-out`}
                    />
                  </span>
                );
              })}
            </div>

            {/* Mobile Menu Button */}
            <div className="sm:hidden">
              <button
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                className="relative z-50 p-2 text-white"
                aria-label="Toggle menu"
              >
                <div className="w-6 h-6 flex flex-col justify-between items-center">
                  <span className={`block w-full h-0.5 bg-current transform transition duration-300 ease-in-out ${isMenuOpen ? 'rotate-45 translate-y-2.5' : ''}`}></span>
                  <span className={`block w-full h-0.5 bg-current transform transition duration-300 ease-in-out ${isMenuOpen ? 'opacity-0' : ''}`}></span>
                  <span className={`block w-full h-0.5 bg-current transform transition duration-300 ease-in-out ${isMenuOpen ? '-rotate-45 -translate-y-2.5' : ''}`}></span>
                </div>
              </button>
            </div>
          </div>
        </div>
      </ClickSpark>

      {/* Mobile Menu Overlay */}
      <div
        className={`sm:hidden fixed inset-0 z-40 bg-black/85 backdrop-blur-xl transition-all duration-300 ease-in-out ${isMenuOpen ? 'opacity-100' : 'opacity-0 pointer-events-none'}`}>
        <div className="flex flex-col items-center justify-center h-screen gap-10 px-6">
          {navItems.map((item, index) => {
            const isActive = (pathname || "/") === item.href;
            return (
              <div key={item.href} className="text-center">
                <Link
                  href={item.href}
                  className={`block text-3xl font-semibold transition-colors duration-200 whitespace-nowrap ${
                    isActive ? 'text-white' : 'text-white/75 hover:text-white'
                  }`}
                  onClick={() => setIsMenuOpen(false)}
                >
                  {item.label}
                </Link>
                {isActive && (
                  <div className="w-12 h-0.5 bg-white/70 mx-auto mt-3"></div>
                )}
              </div>
            );
          })}
        </div>
      </div>
    </nav>
  );
}

